import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable, observable } from 'rxjs';
import 'rxjs/Rx';
import { map, catchError } from 'rxjs/operators';
import { UserModel } from '../Model/UserModel';
import { ParentTaskModel } from '../Model/ParentTaskModel';
import { TaskModel } from '../Model/TaskModel';
import { ProjectModel } from '../Model/ProjectModel';

@Injectable()
export class ManageService {

    constructor(private http: Http) { }


    apiurl : string ='172.18.3.27:85';
    //apiurl: string = 'localhost:51530';

    private AddEditUserURL = "http://" + this.apiurl + "/api/ProjectManager/AddEditUser";
    private AddParentTaskURL = "http://" + this.apiurl + "/api/ProjectManager/AddParentTask";
    private AddProjectURL = "http://" + this.apiurl + "/api/ProjectManager/AddProject";
    private AddTaskURL = "http://" + this.apiurl + "/api/ProjectManager/AddTask";
    private DeleteUserURL = "http://" + this.apiurl + "/api/ProjectManager/DeleteUser";
    private GetProjectDetailsListURL = "http://" + this.apiurl + "/api/ProjectManager/GetProjectDetailsList";
    private GetTaskDetailsListURL = "http://" + this.apiurl + "/api/ProjectManager/GetTaskDetailsList";
    private GetUserDetailsListURL = "http://" + this.apiurl + "/api/ProjectManager/GetUserDetailsList";
    private DeleteProjectURL = "http://" + this.apiurl + "/api/ProjectManager/DeleteProject";

    private GetParentListURL = "http://" + this.apiurl + "/api/ProjectManager/GetParentList";

    GetUserDetailsList(): Observable<UserModel[]> {
        return this.http.get(this.GetUserDetailsListURL, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );

    }
    AddEditUser(userModel: UserModel): Observable<string> {
        return this.http.post(this.AddEditUserURL, userModel, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );

    }
    DeleteUser(userId: any): Observable<string> {
        return this.http.post(this.DeleteUserURL, userId, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );
    }

    DeleteProject(id: any): Observable<string> {
        return this.http.post(this.DeleteProjectURL, id, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );
    }
    GetProjectList(): Observable<ProjectModel[]> {
        return this.http.get(this.GetProjectDetailsListURL, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );

    }
    AddEditProject(projectModel: ProjectModel): Observable<string> {
        return this.http.post(this.AddProjectURL, projectModel, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );

    }
    GetTaskList(): Observable<TaskModel[]> {
        return this.http.get(this.GetTaskDetailsListURL, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );

    }

    GetParentList(): Observable<ParentTaskModel[]> {
        return this.http.get(this.GetParentListURL, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );

    }
    AddEditTask(taskModel: TaskModel): Observable<string> {
        return this.http.post(this.AddTaskURL, taskModel, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );

    }
    AddEditParent(parentModel: ParentTaskModel): Observable<string> {
        return this.http.post(this.AddParentTaskURL, parentModel, this.getRequestOptions()).pipe(
            map(res => res.json()),
            catchError(err => Observable.throw(err))
        );

    }



    private getRequestOptions(): RequestOptions {
        const requestOptions = new RequestOptions();
        const headers: Headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('Accept', 'application/json');
        requestOptions.headers = headers;
        requestOptions.withCredentials = true;
        return requestOptions;
    }
}