import {NgModule} from '@angular/core';
import {Routes, RouterModule, ActivatedRouteSnapshot} from '@angular/router';

import {AddTaskComponent} from './Component/AddTask.component';
import {HomeComponent} from './home.component';
import { ProjectComponent } from './Component/ProjectView.component';
import { UserComponent } from './Component/UserView.component';
import {ViewTaskComponent  } from './Component/ViewTask.component';

export const routes:Routes = [
    
    {path:'home', component:HomeComponent},
    {path:'addTask', component:AddTaskComponent},
    {path:'viewTask', component:ViewTaskComponent},
    {path:'user', component:UserComponent},
    {path:'project', component:ProjectComponent},
    {path:'',redirectTo:'home', pathMatch:'full'},
];

@NgModule(
    {
        imports:[RouterModule.forChild(routes)],
        exports:[RouterModule]
    }
)
export class HomeRoutingModule{}